@media only screen and (max-width: 1440px) and (min-width: 1291px) {
  .blob {
    width: 45%;
    height: 74%;
  }
}
@media only screen and (max-width: 1290px) and (min-width: 1051px) {
  .title-text {
    font-size: 17vw;
  }

  #tribute__name {
    font-size: 29vw;
  }

  .blob {
    width: 50vw;
    height: 50vw;
  }

  .e5,
  .e6 {
    width: 28%;
  }

  .text {
    font-size: 3.5em;
  }
}

@media only screen and (max-width: 1050px) and (min-width: 651px) {
  .blob {
    width: 600px;
    height: 600px;
  }

  .text {
    font-size: 3.5em;
  }

  .e4 {
    left: 35%;
  }
}

@media only screen and (max-width: 650px) {
  .e5,
  .e6 {
    display: none;
  }
}

@media only screen and (max-width: 650px) and (min-width: 481px) {
  .blob {
    top: 10%;
    width: 100%;
    height: 100%;
  }

  .text {
    font-size: 2.5em;
  }
}
@media only screen and (max-width: 412px) {
  .title-text {
    top: 1.55em;
    font-size: 21vh;
  }

  #tribute__name {
    font-size: 43.1vh;
  }
  .blob {
    top: 10%;
    width: 100%;
    height: 100%;
  }

  .text {
    font-size: 2.5em;
  }
}
